@import url('https://fonts.googleapis.com/css?family=Kanit&display=swap');

.App {
  /* text-align: center; */
  font-family: 'Kanit', sans-serif;
}
h1{
  text-align: center;
}


/* .body{
  
} */
.user-container{
  background-color: rgb(240, 248, 248);
  box-shadow: 10px 12px 12px rgba(33, 44, 44, 0.336);
  border: 2px double rgba(33, 44, 44, 0.336);
  display: flex;
  flex-direction: column;
  padding: 5% 10%;
}
.info{
  width: 80%;
  margin: 2% auto;
  display: flex;
  justify-content: space-between;
}
.info-img{
  width: 20vw;
}
.info-text{
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: 5%;
  align-content: flex-start;
}
.people{
  display: flex;
}
.people-display{
  color: blue;
}
.people-div{
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.close{
  display: none;
}
.people-card{
  display: flex;
  justify-content: flex-start;
  margin: 2% 0;
  align-items: center;
  box-shadow: 4px 6px 6px rgba(33, 44, 44, 0.336);
  border: 1px solid rgba(33, 44, 44, 0.336);
}
.people-img{
  width: 10vw;
  margin-right: 5%;
}
.git-chart{
  width: 100%;
  margin: 2% auto;
  padding: 2%;
  text-align: center;
}
.git-chart-img{
  width: 100%;
  margin: 0;
  background-color: white;
}
.git-chart h3{
  margin: 0;
}

.user-form{
  display: flex;
  justify-content: space-evenly;
  margin: 5% 0;
}



